    import mInput from '../../components/m-input.vue';

    export default {
        components: {
            mInput
        },
        data() {
            return {
                time:0,
                times:'',
                phone:'',
                auth:'',
                pwd1:'',
                pwd2:'',
            }
        },
        methods: {
            userAgreement() {
            	uni.navigateTo({
            		url:"../userareement/userareement"
            	})
            },
			regGetAuth() {
				let p = this.phone;
				let data = {
					mobile: p,
					send_type:'reg'
				}
			   uni.request({
			   	url:"http://www.zgkayida.com/api/sms/sendCode",
			   	data:data,
			   	header:{
			   		'content-type': 'application/x-www-form-urlencoded;application/json;charset=UTF-8'
			   	},
			   	method:"POST",
			   	success: (res) => {
			   		if (res.data.code === 1000){
			   			uni.showToast({
			   				title: res.data.msg,
			   				icon: "none"
			   			})
			   			this.time = 1;
			   			this.times = 59;
			   			setInterval(()=>{
			   				this.times --;
			   				if(this.times < 1){
			   					this.time = 2;
			   					clearInterval()
			   				}
			   			},1000)
			   		}
			   		if (res.data.code !== 1000){
			   			uni.showToast({
			   				title: res.data.msg,
			   				icon: "none"
			   			})
			   		}
			   	}
			   })
			},
			userReg() {
				let data = {
					mobile:this.phone,
					code:this.auth,
					password:this.pwd1,
					confirm_password:this.pwd2
				}
			   uni.request({
			   	url:"http://www.zgkayida.com/api/user/reg",
			   	data:data,
			   	header:{
			   		'content-type': 'application/x-www-form-urlencoded;application/json;charset=UTF-8'
			   	},
			   	method:"POST",
			   	success: (res) => {
			   		if(res.data.code !== 1000){
			   			uni.showToast({
			   				title:res.data.msg,
			   				icon:"none"
			   			})
			   		}else if(res.data.code === 1000){
			   			uni.redirectTo({
			   				url:"../login/login"
			   			})
			   		}
			   	}
			   })
			}
        }
    }
